/* eslint-disable no-nested-ternary */
import { memo, useRef } from 'react';
import { I_Scene } from '@containers/Home/types';
import { DEFAULT_VALUE_FLOORPLAN_CONFIG } from '@containers/Home/constants';
import dynamic from 'next/dynamic';
import { useTourEventState } from '@containers/Home/global/TourStats';
import { useEventFloorplan } from '@containers/Home/hook/useEventFloorplan';
import { useSelector } from 'react-redux';
import { selectThemeConfigGlobal } from '@containers/Home/selectors';
import { ActionFloorplanWrapper, FloorplanPanelWrapper } from './styled';
import ImageFloorplan, { Type_RefFuncImageFloorplan } from './ImageFloorplan';

const DropdownContainer = dynamic(
  () => import('@components/DropdownContainer'),
);
const Popup = dynamic(() => import('./Popup'));

interface Props {
  onChangeScene: (scene: I_Scene) => void;
}

const FloorplanPanel = memo(({ onChangeScene }: Props) => {
  const floorplanRef = useRef<Type_RefFuncImageFloorplan>(null!);

  const themeConfig = useSelector(selectThemeConfigGlobal);
  const [tourEventStats] = useTourEventState('state');

  const {
    visiblePopup,
    sceneSelected,
    listFloorplan,
    markerSelected,
    configFloorplan,
    floorplanSelected,
    configDisplayPanel,
    onCenterToMarkerActive,
    onChangeFloorplan,
    onCloseFloorplan,
    onClosePopup,
    onOpenPopup,
    onZoomOut,
    onZoomIn,
  } = useEventFloorplan({
    floorplanRef,
  });

  return (
    <FloorplanPanelWrapper
      bgColor={
        configFloorplan?.bgColor || DEFAULT_VALUE_FLOORPLAN_CONFIG.bgColor
      }
      position={configDisplayPanel?.position || 'left'}
      className={`${tourEventStats.isVisibleFloorplan ? '' : 'hide'} ${
        themeConfig?.selected === 'basekrpano'
          ? 'basekrpano'
          : themeConfig?.selected === 'custom1'
          ? 'custom1'
          : themeConfig?.selected === 'wall'
          ? 'wall'
          : 'default'
      }`}
    >
      <div className="main">
        {floorplanSelected && (
          <ImageFloorplan
            zoom={1}
            ref={floorplanRef}
            sceneSelected={sceneSelected}
            markerSelected={markerSelected}
            floorplanSelected={floorplanSelected}
            configFloorplan={configFloorplan}
            onChangeScene={onChangeScene}
          />
        )}
      </div>

      <ActionFloorplanWrapper>
        <div className="--left">
          <DropdownContainer
            menuActive={[floorplanSelected?.id]}
            listMenu={listFloorplan.map(floorplan => ({
              type: floorplan.id,
              classIcon: 'far fa-project-diagram',
              label: floorplan.name,
            }))}
            onClick={onChangeFloorplan}
          >
            <div className="action--item --select">
              <span>{floorplanSelected?.name || '---'}</span>
              <i aria-hidden className="fas fa-caret-down" />
            </div>
          </DropdownContainer>
        </div>
        <div className="--right">
          <div className="action--item" onClick={onZoomIn}>
            <i aria-hidden className="far fa-search-plus" />
          </div>
          <div className="action--item" onClick={onZoomOut}>
            <i aria-hidden className="far fa-search-minus" />
          </div>
          <div className="action--item" onClick={onCenterToMarkerActive}>
            <i aria-hidden className="far fa-crosshairs" />
          </div>
          <div className="action--item" onClick={onOpenPopup}>
            <i aria-hidden className="far fa-expand" />
          </div>
          <div className="action--item" onClick={onCloseFloorplan}>
            <i aria-hidden className="far fa-times" />
          </div>
        </div>
      </ActionFloorplanWrapper>

      {visiblePopup && (
        <Popup
          visible={visiblePopup}
          listFloorplan={listFloorplan}
          markerSelected={markerSelected}
          floorplanSelected={floorplanSelected}
          configFloorplan={configFloorplan}
          onChangeFloorplan={onChangeFloorplan}
          onChangeScene={onChangeScene}
          onCancel={onClosePopup}
        />
      )}
    </FloorplanPanelWrapper>
  );
});

export default FloorplanPanel;
